class SidebarFixer {

	options = {
		limiter: 640,
		elements: {
			sidebar: document.getElementById('sidebar'),
			toggler: document.getElementById('sidebarToggler'),
		}
	}

	constructor (options = {}) {
		this.options = Object.assign(this.options, options)

		window.onload = window.onresize = () => {
			this.toggleFixed()
		}

		window.onclick = (e) => {
			if (this.options.elements.sidebar && !this.isCollapse && this.isFixed) {
				e.preventDefault()
				let target = e.target
				let sidebarTarget = this.options.elements.sidebar?.contains(target) === true
				let togglerTarget = this.options.elements.toggler?.contains(target) === true

				if (!sidebarTarget && !togglerTarget) {
					this.options.elements.toggler?.click()
				}
			}
		}
	}

	get isFixed () {
		return this.options.elements.sidebar?.classList.contains('fixed') === true
	}

	get isCollapse () {
		return this.options.elements.sidebar?.classList.contains('collapsed') === true
	}

	get shouldFixed () {
		return window.innerWidth < this.options.limiter
	}

	toggleFixed () {
		const { sidebar } = this.options.elements

		if (!this.isFixed && this.shouldFixed) sidebar?.classList.add('fixed')
		else if (this.isFixed && !this.shouldFixed) sidebar?.classList.remove('fixed')
	}

}

export default SidebarFixer
