class SidebarCollapser {

	options = {
		limiter: 640,
		activeClass: 'collapsed',
		elements: {
			toggler: document.getElementById('sidebarToggler'),
			sidebar: document.getElementById('sidebar')
		}
	}

	constructor (options = {}) {
		this.options = Object.assign(this.options, options)

		if (this.options.elements.toggler) {
			this.options.elements.toggler.onclick = (e) => {
				e.preventDefault()
				this.toggleCollapse()
			}
		}
	}

	get isCollapse () {
		return this.options.elements.sidebar?.classList.contains('collapsed') === true
	}

	get shouldCollapse () {
		return window.innerWidth < this.options.limiter
	}

	toggleCollapse() {
		let { sidebar } = this.options.elements
		if (this.isCollapse) sidebar?.classList.remove('collapsed')
		else sidebar?.classList.add('collapsed')
	}

}

export default SidebarCollapser
